import React, { useState } from 'react'
import { Row, Col } from 'react-bootstrap';
import MetaTags from './MetaTags';
import RobotTxt from './RobotTxt';
import RSS from './RSSFeed';
import Schema from './Schema';
import SEOTab from './SEO';
import Sitemap from './Sitemap';

const SEO = () => {
  const tab = [
    "SEO",
    "MetaTags",
    "Robots.Txt",
    "Sitemap",
    "RSS Feed",
    "Schema"
  ];
  const [selectedTab, setSelectedTab] = useState(tab[0]);
  return (
    <div className="general">
      <Row>
        <Col lg={12}>
          <h3>SEO</h3>
        </Col>
      </Row>
      <Row>
        <Col lg={12} className="">
          {tab.map((item) => (
            <p
              className={`tab ${item == selectedTab ? "active" : ""}`}
              onClick={() => setSelectedTab(item)}
            >
              {item}
            </p>
          ))}
        </Col>
      </Row>
      <Row>
        <Col lg={12} className="mt-4">
          {selectedTab == tab[0] ? <SEOTab /> : ""}
          {selectedTab == tab[1] ? <MetaTags /> : ""}
          {selectedTab == tab[2] ? <RobotTxt /> : ""}
          {selectedTab == tab[3] ? <Sitemap /> : ""}
          {selectedTab == tab[4] ? <RSS /> : ""}
          {selectedTab == tab[5]? <Schema/>:""}
        </Col>
      </Row>
    </div>
  );
};

export default SEO;