import React, { useRef, useState } from 'react'
import { Row, Col } from 'react-bootstrap';
import placeholder from "./../../../../../Assets/MyStore/placeholder-logo.svg";
import SwitchWidget from "../../../SwitchWidget";

const Others = () => {
  const [showFooter, setShowFooter] = useState(false);
  const [showStarted, setShowStarted] = useState(false);
  const inputFile = useRef(null);
  const uploadImage = () => {
    // `current` points to the mounted file input element
    inputFile.current.click();
  }; 

  return (
    <div>
      <Row>
        <Col lg={12}>
          <Row>
            <Col lg={12}>
              <h4>Others</h4>
            </Col>
          </Row>
          <Row className="mt-5">
            <Col lg={6}>
              <SwitchWidget
                isChecked={showFooter}
                setIsChecked={setShowFooter}
                title={"Footer section"}
                subtitle={"Turn this ON if you want to show Footer section"}
              />
            </Col>
            <Col lg={6}>
              <SwitchWidget
                isChecked={showStarted}
                setIsChecked={setShowStarted}
                title={"Getting Started section"}
                subtitle={
                  "Turn this ON if you want to show Getting Started section in home page"
                }
              />
            </Col>
          </Row>
          <Row className="mt-2">
            <Col md={6}>
              <div>
                <p className='mb-1'>Store Loader</p>
                <input
                  type="file"
                  id="file"
                  ref={inputFile}
                  className="d-none"
                />
                <img
                  className="logo-img"
                  src={placeholder}
                  alt=""
                  onClick={uploadImage}
                />
              </div>
            </Col>
          </Row>
        </Col>
      </Row>
    </div>
  );
}

export default Others;