import React, { useEffect, useState } from "react";
import { Row, Col, Form, Button } from "react-bootstrap";
import SwitchWidget from "../../../SwitchWidget";

const BasicSettings = () => {
  const [showInSuperMarket, setShowInSuperMarket] = useState(false);
  const [marketPlaceAsHome, setMarketPlaceAsHome] = useState(false);
  const [showLanguage, setShowLanguage] = useState(false);

  return (
    <div>
      <Row>
        <Col lg={12}>
          <Row>
            <Col lg={12}>
              <h4>Basic Settings</h4>
            </Col>
          </Row>
          <Row className="mt-5">
            <Col lg={6}>
              <SwitchWidget
                isChecked={showInSuperMarket}
                setIsChecked={setShowInSuperMarket}
                title={"Show my Stores's NFTs in NFTically Super Market"}
                subtitle={
                  "Turn this ON if you want to show your NFTs in NFTically Super Market"
                }
              />
            </Col>
          </Row>
          <Row>
            <Col lg={6}>
              <SwitchWidget
                isChecked={marketPlaceAsHome}
                setIsChecked={setMarketPlaceAsHome}
                title={"Set Marketplace page as Homepage"}
                subtitle={
                  "Turn this ON if you want to use Marketplace page as Homepage"
                }
              />
            </Col>
          </Row>
          <Row>
            <Col lg={6}>
              <SwitchWidget
                isChecked={showLanguage}
                setIsChecked={setShowLanguage}
                title={"Show Language Change Button"}
                subtitle={
                  "Turn this ON if you want to show Marketplace language switch button"
                }
              />
            </Col>
          </Row>
          <Row>
            <Col lg={6}>
              <Form.Group className="mb-4 d-flex flex-column">
                <Form.Label>Locale</Form.Label>
                <select className="form-control">
                  <option>1</option>
                  <option>2</option>
                  <option>3</option>
                  <option>4</option>
                  <option>5</option>
                </select>
              </Form.Group>
            </Col>
            <Col lg={6}>
              <Form.Group className="mb-4 d-flex flex-column">
                <Form.Label>Time Zone</Form.Label>
                <select className="form-control">
                  <option>1</option>
                  <option>2</option>
                  <option>3</option>
                  <option>4</option>
                  <option>5</option>
                </select>
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col lg={6}>
              <Form.Group className="mb-4 d-flex flex-column">
                <Form.Label>Date Format</Form.Label>
                <select className="form-control">
                  <option>1</option>
                  <option>2</option>
                  <option>3</option>
                  <option>4</option>
                  <option>5</option>
                </select>
              </Form.Group>
            </Col>
            <Col lg={6}>
              <Form.Group className="mb-4 d-flex flex-column">
                <Form.Label>Time Format</Form.Label>
                <select className="form-control">
                  <option>1</option>
                  <option>2</option>
                  <option>3</option>
                  <option>4</option>
                  <option>5</option>
                </select>
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col>
              <Button variant="primary">Save Changes</Button>
            </Col>
          </Row>
        </Col>
      </Row>
    </div>
  );
};

export default BasicSettings;
