import React, { useRef } from 'react'
import { Row, Col, Button, Form } from 'react-bootstrap'
import placeholder from "./../../../../../Assets/MyStore/placeholder-logo.svg"
import "./name.scss"

const Name = () => {
  const inputFile = useRef(null);
   const uploadImage = () => {
     // `current` points to the mounted file input element
     inputFile.current.click();
   }; 

  return (
    <div>
      <Row>
        <Col lg={12}>
          <Row>
            <Col lg={12}>
              <h4>My Store</h4>
            </Col>
          </Row>
          <Row className="mt-4">
            <Col md={6}>
              <div>
                <p>Logo</p>
                <input
                  type="file"
                  id="file"
                  ref={inputFile}
                  className="d-none"
                />
                <img
                  className="logo-img"
                  src={placeholder}
                  alt=""
                  onClick={uploadImage}
                />
                <p className="mt-2 logo-sub">
                  Please upload a .svg file of your logo in 400x300 px size or
                  similar ratio
                </p>
              </div>
            </Col>
            <Col md={6}>
              <Form>
                <Form.Group className="mb-4">
                  <Form.Label>Store Name</Form.Label>
                  <Form.Control type="text" placeholder="Enter Store Name" />
                </Form.Group>

                <Form.Group className="mb-4">
                  <Form.Label>E-Mail</Form.Label>
                  <Form.Control type="email" placeholder="Enter email" />
                </Form.Group>

                <Form.Group className="mb-4 d-flex flex-column">
                  <Form.Label>Country</Form.Label>
                  <select className="form-control">
                    <option>1</option>
                    <option>2</option>
                    <option>3</option>
                    <option>4</option>
                    <option>5</option>
                  </select>
                </Form.Group>
              </Form>
            </Col>
          </Row>
          <Row className="mt-5">
            <Col lg={12}>
              <p>Visit Store: XYZ</p>
            </Col>
              <Col>
                <Button variant="primary">Save Changes</Button>
              </Col>
          </Row>
        </Col>
      </Row>
    </div>
  );
}

export default Name