import React, { useEffect, useState } from "react";
import { useHistory, useRouteMatch } from "react-router-dom";
import "./style.scss";
import generalLogo from "./../../../Assets/MyStore/general-logo.svg";
import placeholder from "./../../../Assets/MyStore/placeholder-dp.svg";


const SettingsList = ({settingsItems}) => {
  const history = useHistory();
  const match = useRouteMatch();
  // const settingsItems = [
  //   { logo: "", title: "General", link: "General" },
  //   { logo: "", title: "Advance", link: "Advance"},
  //   { logo: "", title: "SEO", link: "SEO" },
  //   { logo: "", title: "Appearance", link: "Appearance"},
  //   { logo: "", title: "Billing", link: "Billing"},
  //   { logo: "", title: "Team", link: "Team"},
  //   {
  //     logo: "",
  //     title: "Analytics & Integration",
  //     link: "Analytics"
  //   },
  //   { logo: "", title: "Blog", link: "Blog" },
  //   { logo: "", title: "API", link: "API"},
  //   { logo: "", title: "Support", link: "Support" },
  // ];

  const [selectedTab, setSelectedTab] = useState(settingsItems[0].link);

  const handleClick = (link: string) => {
    history.replace(`${match.path}/${link}`);
    setSelectedTab(link);
  };

  useEffect(() => {
    history.replace(`${match.path}/${settingsItems[0].link}`);
    setSelectedTab(settingsItems[0].link);
  }, []);

  return (
    <div className="settingsList">
      <div className="text-center">
        <img className="mt-3" src={placeholder} alt="" />
        <p className="border-bottom mt-3 pb-3 userName">Sid Patel</p>
      </div>
      <ul>
        {settingsItems.map((item) => (
          <li key={item.link} onClick={() => handleClick(item.link)}>
            <a
              className={`cursor-pointer ${
                selectedTab == item.link ? "active" : ""
              }`}
            >
              <img src={generalLogo} alt="" />
              <span>{item.title}</span>
            </a>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default SettingsList;
