import React, { useState } from 'react'
import { Row, Col } from 'react-bootstrap';
import Categories from './Categories';
import CustomPage from './CustomPage';
import KYC from './KYC';
import SiteUrl from './SiteUrl';
import SupportedNetwork from './SupportedNetwork';

const Advance = () => {
  const tab = [
    "Supported Network",
    "KYC Settings",
    "Categories",
    "Site URLs",
    "Custom Store Pages",
  ];
  const [selectedTab, setSelectedTab] = useState(tab[0]);
  return (
    <div className="general">
      <Row>
        <Col lg={12}>
          <h3>Advance</h3>
        </Col>
      </Row>
      <Row>
        <Col lg={12} className="">
          {tab.map((item) => (
            <p
              className={`tab ${item == selectedTab ? "active" : ""}`}
              onClick={() => setSelectedTab(item)}
            >
              {item}
            </p>
          ))}
        </Col>
      </Row>
      <Row>
        <Col lg={12} className="mt-4">
          {selectedTab == tab[0] ? <SupportedNetwork /> : ""}
          {selectedTab == tab[1] ? <KYC /> : ""}
          {selectedTab == tab[2] ? <Categories /> : ""}
          {selectedTab == tab[3] ? <SiteUrl /> : ""}
          {selectedTab == tab[4] ? <CustomPage /> : ""}
        </Col>
      </Row>
    </div>
  );
};

export default Advance