import React, { useState } from "react";
import { Row, Col, Button, Form } from "react-bootstrap";
import SwitchWidget from "../../../SwitchWidget";
import themeBlue from "./../../../../../Assets/MyStore/theme-blue.svg";
import "./theme.scss";

const Theme = () => {
  const themes = [
    { name: "Default Blue", image: "" },
    { name: "Subtle Green", image: "" },
    { name: "Royal Orange", image: "" },
    { name: "Royal Purple", image: "" },
    { name: "Smoky Black", image: "" },
    { name: "Apple Red", image: "" },
  ];
  const [selected, setSelected] = useState(themes[0].name);
  const [enableDarkMode, setEnableDarkMode] = useState(false);
  return (
    <div>
      <Row>
        <Col lg={12}>
          <Row>
            <Col lg={12}>
              <h4>Color Palette</h4>
            </Col>
          </Row>
          <Row>
            <Col lg={12}>
              <ul className="themeSelection p-0">
                {themes.map((item) => (
                  <li
                    className={`d-flex flex-column cursor-pointer ${
                      selected == item.name ? "active" : ""
                    }`}
                    onClick={() => setSelected(item.name)}
                  >
                    <img src={themeBlue} alt="" />
                    <p className="mt-3">{item.name}</p>
                  </li>
                ))}
              </ul>
            </Col>
          </Row>
          <Row className="mt-5">
            <Col lg={6}>
              <SwitchWidget
                isChecked={enableDarkMode}
                setIsChecked={setEnableDarkMode}
                title={"Dark Mode"}
                subtitle={
                  "Turn it on to switch the current theme from light to dark mode"
                }
              />
            </Col>
          </Row>
          <Row>
            <Col>
              <Button variant="primary">Save Changes</Button>
            </Col>
          </Row>
        </Col>
      </Row>
    </div>
  );
};

export default Theme;
