import React from 'react'
import { Row, Col, Button, Table, Form } from 'react-bootstrap';

const HeaderSection = () => {
  return (
    <div>
      <Row>
        <Col lg={12}>
          <Row>
            <Col lg={12}>
              <h4>Header Section</h4>
            </Col>
          </Row>
          <Row className="mt-3">
            <Col lg={12}>
              <Form.Group>
                <Form.Label>Heading</Form.Label>
                <Form.Control type="text" placeholder="Enter Heading" />
              </Form.Group>
            </Col>
          </Row>
          <Row className="mt-3">
            <Col lg={12}>
              <Form.Group>
                <Form.Label>Description</Form.Label>
                <Form.Control type="text" placeholder="Enter Description" />
              </Form.Group>
            </Col>
          </Row>
          <Row className="mt-5">
            <Col lg={12}>
              <p>Buttons</p>
              <Table variant="dark" responsive>
                <thead>
                  <tr>
                    <th>Button</th>
                    <th>Title</th>
                    <th>Link</th>
                    <th>New Tab</th>
                    <th>Style</th>
                    <th>Show/Hide</th>
                  </tr>
                </thead>
                <tbody></tbody>
              </Table>
            </Col>
          </Row>
        </Col>
      </Row>
    </div>
  );
}

export default HeaderSection;