import React, { useState } from "react";
import { Row, Col, Form, Button } from "react-bootstrap";
import SwitchWidget from "../../../SwitchWidget";

const ContactUs = () => {
  const [showNewsLetter, setShowNewsLetter] = useState(true);
  const [showContactUs, setShowContactUs] = useState(true);

  return (
    <div>
      <Row>
        <Col lg={12}>
          <Row>
            <Col lg={12}>
              <h4>Contact Us</h4>
            </Col>
          </Row>
          <Row className="mt-5">
            <Col lg={6}>
              <Form.Group className="mb-4 d-flex flex-column">
                <Form.Label>Phone</Form.Label>
                <Form.Control type="text" placeholder="Enter Phone Number" />
              </Form.Group>
            </Col>
            <Col lg={6}>
              <Form.Group className="mb-4 d-flex flex-column">
                <Form.Label>Email</Form.Label>
                <Form.Control type="text" placeholder="Enter Email" />
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col lg={12}>
              <Form.Group className="mb-4 d-flex flex-column">
                <Form.Label>Address</Form.Label>
                <Form.Control
                  as="textarea"
                  placeholder="Enter Address"
                  rows={5}
                />
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col>
              <Button variant="primary">Save Changes</Button>
            </Col>
          </Row>
          <Row className="mt-5">
            <Col lg={12}>
              <h4>Toggle Settings</h4>
            </Col>
          </Row>
          <Row className="mt-5">
            <Col lg={6}>
              <SwitchWidget
                isChecked={showNewsLetter}
                setIsChecked={setShowNewsLetter}
                title={"Newsletter Subscription"}
                subtitle={
                  "Turn it on/off to show/hide the newsletter subscription section"
                }
              />
            </Col>
            <Col lg={6}>
              <SwitchWidget
                isChecked={showContactUs}
                setIsChecked={setShowContactUs}
                title={"Contact Us"}
                subtitle={"Turn it on/off to show/hide the contact us section"}
              />
            </Col>
          </Row>
        </Col>
      </Row>
    </div>
  );
};

export default ContactUs;
