import React, { useRef } from "react";
import { Row, Col, Form, Button } from "react-bootstrap";
import placeholder from "./../../../../../Assets/MyStore/placeholder-logo.svg";
import "./seoTab.scss"

const SEOTab = () => {
  const inputFile = useRef(null);
  const uploadImage = () => {
    // `current` points to the mounted file input element
    inputFile.current.click();
  };
  return (
    <div>
      <Row>
        <Col lg={12}>
          <Row>
            <Col lg={12}>
              <h4>SEO</h4>
            </Col>
          </Row>
          <Row className="mt-5">
            <Col md={6}>
              <Form.Group>
                <Form.Label>Meta Title</Form.Label>
                <Form.Control type="text" placeholder="Enter Meta Title" />
              </Form.Group>
            </Col>
            <Col md={6}>
              <Form.Group>
                <Form.Label>Meta Keyword</Form.Label>
                <Form.Control type="text" placeholder="Enter Meta Keyword" />
              </Form.Group>
            </Col>
          </Row>
          <Row className="mt-4">
            <Col md={6}>
              <Form.Group>
                <Form.Label>Meta Description</Form.Label>
                <Form.Control
                  as="textarea"
                  placeholder="Enter Description"
                  rows={8}
                />
              </Form.Group>
            </Col>
            <Col md={6}>
              <div>
                <p className="mb-2">Favicon</p>
                <input
                  type="file"
                  id="file"
                  ref={inputFile}
                  className="d-none"
                />
                <img
                  className="seo-img"
                  src={placeholder}
                  alt=""
                  onClick={uploadImage}
                />
                <p className="mt-2 logo-sub">
                  Please upload a file in 256x256px size or similar ratio.
                </p>
              </div>
            </Col>
          </Row>
          <Row>
            <Col md={6}>
              <div>
                <p className="mb-2">Featured Image</p>
                <input
                  type="file"
                  id="file"
                  ref={inputFile}
                  className="d-none"
                />
                <img
                  className="seo-img"
                  src={placeholder}
                  alt=""
                  onClick={uploadImage}
                />
                <p className="mt-2 logo-sub">
                  Please upload a file in 750x430px size or similar ratio.
                </p>
              </div>
            </Col>
          </Row>
          <Row>
            <Col>
              <Button variant="primary">Save Changes</Button>
            </Col>
          </Row>
        </Col>
      </Row>
    </div>
  );
};

export default SEOTab;
