import React from 'react'
import { Row, Col, Form, Button } from 'react-bootstrap';

const MetaTags = () => {
  return (
    <div>
      <Row>
        <Col lg={12}>
          <Row>
            <Col lg={12}>
              <h4>Meta Tags</h4>
            </Col>
          </Row>
          <Row className="mt-5">
            <Col md={6}>
              <Form.Group>
                <Form.Label>Meta Tags</Form.Label>
                <Form.Control
                  as="textarea"
                  placeholder="Enter Meta Tags"
                  rows={5}
                />
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col>
              <Button variant="primary">Save Changes</Button>
            </Col>
          </Row>
        </Col>
      </Row>
    </div>
  );
}

export default MetaTags;